import React, { useState } from 'react';
import { Form, Button, Container } from 'react-bootstrap';

function ContactForm() {

    const [name, setName] = useState('')
    const [email, setEmail] = useState('')
    const [massage, setMassage] = useState('')

    return (
        <section id="contact">
            <Container fluid>
                <Form>
                    <Form.Group controlId="formBasicPassword">
                        <Form.Label>Name</Form.Label>
                        <Form.Control name="name" type="text" placeholder="Enter name" value={name} onChange={(event) => { setName(event.target.value) }} />
                    </Form.Group>

                    <Form.Group controlId="formBasicEmail">
                        <Form.Label>Email</Form.Label>
                        <Form.Control name="email" type="email" placeholder="Enter email" value={email} onChange={(event) => { setEmail(event.target.value) }} />
                    </Form.Group>

                    <Form.Group controlId="formBasicEmail">
                        <Form.Label>Message</Form.Label>
                        <Form.Control name="massage" as="textarea" row="4" placeholder="Enter Message" value={massage} onChange={(event) => { setMassage(event.target.value) }} />
                    </Form.Group>

                    <Button variant="primary" type="submit">
                        Send {'>>'}
                    </Button>
                </Form>
            </Container>
        </section>
    )
}

export default ContactForm;
