import React from 'react'
import { Container, Row, Col, Button } from 'react-bootstrap'

function AboutMeBroad() {
    return (
        <section id="about">
            <Container fluid>
                <Row>
                    <Col lg="7" className="d-flex align-items-center">
                        <Container id="about-title">
                            <h1>Hi I'm Vachara Vongvittayamethkul</h1>
                            <h3>Programmer | Web Developer</h3>
                            <Button onClick={() => { window.location.href = process.env.PUBLIC_URL + "#contact" }} className="about-button btn-dark">Contact Me</Button>
                            <a href={process.env.PUBLIC_URL + "/Vachara's Resume.pdf"} rel="noopener noreferrer" target="_blank"><Button className="about-button btn-dark">My Resume</Button></a>
                        </Container>
                    </Col>
                    <Col lg="5" className="d-flex justify-content-end align-items-center">
                        <Container> 
                            <img id="about-image" src={process.env.PUBLIC_URL + '/my_profile.png'} alt="profile" />
                        </Container>
                    </Col>
                </Row>
            </Container>
        </section>
    )
}

export default AboutMeBroad